

.first-clump {
  background-color: cornsilk;
  font-size: 20px;
}

hr {
  border: 0;
  height: 1px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(100, 100, 100, 0)), color-stop(rgba(100, 100, 100, 0.75)), to(rgba(100, 100, 100, 0)));
  background-image: -webkit-linear-gradient(left, rgba(100, 100, 100, 0), rgba(100, 100, 100, 0.75), rgba(100, 100, 100, 0));
  background-image: -o-linear-gradient(left, rgba(100, 100, 100, 0), rgba(100, 100, 100, 0.75), rgba(100, 100, 100, 0));
  background-image: linear-gradient(to right, rgba(100, 100, 100, 0), rgba(100, 100, 100, 0.75), rgba(100, 100, 100, 0));
}

li {
  margin-bottom: 10px;
}

p {
  text-align: left;
}



